import { Component, OnInit } from '@angular/core';
import { GameService } from 'src/services/game.service';
import { UtilsService } from '../../services/utils.service';
import { Router } from '@angular/router';
import { game } from 'src/models/sample';
import { InfoService } from '../../services/info.service';
import { LocationService } from '../../services/location.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  constructor(
    private gameService: GameService,
    private utilService: UtilsService,
    private router: Router,
    private info: InfoService,
    private locationService: LocationService
  ) {

  }

  ngOnInit() {
  }

  async userLocationAccess() {
    try {
      await this.locationService.getLocation();
    } catch (err) {
      console.log(err);

      throw new Error('You need to give location permission');
    }
  }

  async startGame() {
    try {
      await this.userLocationAccess();
      // this.utilService.loadStory()
      // eslint-disable-next-line no-underscore-dangle
      this.gameService.setGame(game);
      this.router.navigate(['/tabs/map']);
    } catch (err) {
      this.info.alert(err.message);
    }
  }

}
